"use client"

import { useRef, useEffect, useState, useCallback, RefObject } from "react"
import ControlPanel from "../ui/control-panel"
import { TrendingUp, Ruler, Activity, RotateCcw, SkipBack, Play, Pause, SkipForward, Hand } from "lucide-react"
import { useSpringPhysics } from "@/hooks/use-spring-physics"
import { useCanvasScale } from "@/hooks/use-canvas-scale"
import { drawSpring, drawArrow } from "@/lib/canvas-utils"

export default function SpringOscillator() {
  const containerRef = useRef<HTMLDivElement>(null)
  const canvasRef = useRef<HTMLCanvasElement>(null)
  const energyCanvasRef = useRef<HTMLCanvasElement>(null)
  const animationRef = useRef<number>()

  const [layout, setLayout] = useState<"horizontal" | "vertical">("horizontal")
  const [isDragging, setIsDragging] = useState(false)

  // Responsive Hooks
  const { width, height, pixelRatio } = useCanvasScale(containerRef)

  const physics = useSpringPhysics({
    mass: 0.5,
    k: 20,
    amplitude: 80,
    phase: 0
  })

  // Destructure for easy access
  const { params, setParams, isPlaying, setIsPlaying, time, timeRef, historyRef, calculateState, step, reset, derived } = physics

  // Interaction Check
  const checkInteraction = useCallback((clientX: number, clientY: number) => {
    if (!canvasRef.current || !width || !height) return null

    const rect = canvasRef.current.getBoundingClientRect()
    const x = clientX - rect.left
    const y = clientY - rect.top

    let massX, massY
    const state = calculateState(timeRef.current)

    if (layout === "horizontal") {
      massX = (width / 2) + state.x
      massY = height / 2
    } else {
      massX = width / 2
      massY = (height / 2) + state.x
    }

    // Check distance to mass center (approx radius 25px)
    const dist = Math.sqrt(Math.pow(x - massX, 2) + Math.pow(y - massY, 2))
    if (dist < 40) return true // Hit radius
    return false
  }, [layout, width, height, calculateState, timeRef])

  const handlePointerDown = (e: React.PointerEvent) => {
    if (checkInteraction(e.clientX, e.clientY)) {
      setIsDragging(true)
      setIsPlaying(false) // Pause when grabbing
      e.currentTarget.setPointerCapture(e.pointerId)
    }
  }

  const handlePointerMove = (e: React.PointerEvent) => {
    if (!isDragging || !width || !height) return

    const rect = canvasRef.current!.getBoundingClientRect()
    const x = e.clientX - rect.left
    const y = e.clientY - rect.top

    let delta = 0
    // Calculate new position relative to equilibrium
    if (layout === "horizontal") {
      const equilibriumX = width / 2
      delta = x - equilibriumX
    } else {
      const equilibriumY = height / 2
      delta = y - equilibriumY
    }

    // Update Amplitude and Phase based on drag position
    // If delta is positive, Phase = 0, Amp = delta
    // If delta is negative, Phase = PI, Amp = abs(delta)
    const newAmp = Math.abs(delta)
    const newPhase = delta >= 0 ? 0 : Math.PI

    // Clamp Amplitude to reasonable values (e.g., within screen bounds)
    const maxAmp = layout === "horizontal" ? width / 2 - 20 : height / 2 - 20
    const clampedAmp = Math.min(Math.max(newAmp, 0), maxAmp)

    // Force time to 0 so we see the static displacement x = A * cos(phi)
    // This makes it feel like we are directly controlling the position
    timeRef.current = 0
    // We update params directly
    setParams(prev => ({ ...prev, amplitude: clampedAmp, phase: newPhase }))

    // Clear history because we changed initial conditions
    historyRef.current = []
  }

  const handlePointerUp = (e: React.PointerEvent) => {
    setIsDragging(false)
    e.currentTarget.releasePointerCapture(e.pointerId)
  }

  const draw = useCallback(
    (ctx: CanvasRenderingContext2D, t: number) => {
      // Use dynamic width/height
      const currWidth = width
      const currHeight = height
      const state = calculateState(t)

      ctx.clearRect(0, 0, currWidth, currHeight)

      // Grid
      ctx.strokeStyle = "#1e293b"
      ctx.lineWidth = 1
      ctx.beginPath()
      for (let i = 0; i < currWidth; i += 40) {
        ctx.moveTo(i, 0)
        ctx.lineTo(i, currHeight)
      }
      for (let i = 0; i < currHeight; i += 40) {
        ctx.moveTo(0, i)
        ctx.lineTo(currWidth, i)
      }
      ctx.stroke()

      if (layout === "horizontal") {
        const centerY = currHeight / 2
        const wallX = 60
        const equilibriumX = currWidth / 2
        const massX = equilibriumX + state.x

        // Wall
        ctx.fillStyle = "#64748b"
        ctx.fillRect(wallX - 10, centerY - 50, 10, 100)
        ctx.strokeStyle = "#475569"
        ctx.beginPath()
        ctx.moveTo(wallX, centerY + 30)
        ctx.lineTo(currWidth, centerY + 30)
        ctx.stroke()

        // Limits -A, +A
        ctx.strokeStyle = "rgba(250, 204, 21, 0.3)"
        ctx.setLineDash([5, 5])
        ctx.beginPath()
        ctx.moveTo(equilibriumX - params.amplitude, centerY - 60)
        ctx.lineTo(equilibriumX - params.amplitude, centerY + 60)
        ctx.moveTo(equilibriumX + params.amplitude, centerY - 60)
        ctx.lineTo(equilibriumX + params.amplitude, centerY + 60)
        ctx.stroke()
        ctx.setLineDash([])

        ctx.fillStyle = "rgba(250, 204, 21, 0.7)"
        ctx.font = "10px monospace"
        ctx.textAlign = "left"
        ctx.fillText("-A", equilibriumX - params.amplitude - 15, centerY - 65)
        ctx.fillText("+A", equilibriumX + params.amplitude - 5, centerY - 65)

        // Equilibrium Line
        ctx.strokeStyle = "#4ade80"
        ctx.setLineDash([5, 5])
        ctx.beginPath()
        ctx.moveTo(equilibriumX, centerY - 70)
        ctx.lineTo(equilibriumX, centerY + 70)
        ctx.stroke()
        ctx.setLineDash([])
        ctx.fillStyle = "#4ade80"
        ctx.textAlign = "right"
        ctx.fillText("VTCB (O)", equilibriumX - 10, centerY - 75)

        // Draw Spring
        ctx.strokeStyle = "#cbd5e1"
        drawSpring(ctx, wallX, centerY, massX - 25, centerY, 15, 15)

        // Draw Mass
        ctx.fillStyle = isDragging ? "#60a5fa" : "#3b82f6" // Highlight when dragging
        ctx.fillRect(massX - 25, centerY - 25, 50, 50)
        ctx.strokeStyle = isDragging ? "#93c5fd" : "#60a5fa"
        ctx.lineWidth = isDragging ? 3 : 1
        ctx.strokeRect(massX - 25, centerY - 25, 50, 50)

        // Vectors
        if (!isDragging) {
          drawArrow(ctx, massX, centerY, state.v * 0.5, "#22c55e", "v")
          drawArrow(ctx, massX, centerY + 35, state.force * 5, "#ef4444", "F_kv")
        }

      } else {
        // Vertical Layout
        const centerX = currWidth / 2
        const ceilingY = 40
        const equilibriumY = currHeight / 2

        const deltaL_px = derived.deltaL0 * 100 * 1.5 // Scaling for visibility
        const naturalY = equilibriumY - deltaL_px
        const massY = equilibriumY + state.x

        // Ceiling
        ctx.fillStyle = "#64748b"
        ctx.fillRect(centerX - 50, ceilingY - 10, 100, 10)

        // Natural Length Line
        ctx.strokeStyle = "#fbbf24"
        ctx.setLineDash([4, 4])
        ctx.beginPath()
        ctx.moveTo(centerX - 120, naturalY)
        ctx.lineTo(centerX + 120, naturalY)
        ctx.stroke()
        ctx.fillStyle = "#fbbf24"
        ctx.textAlign = "right"
        ctx.fillText("Lò xo không giãn (L0)", centerX - 130, naturalY + 4)

        // Equilibrium Line
        ctx.strokeStyle = "#4ade80"
        ctx.beginPath()
        ctx.moveTo(centerX - 120, equilibriumY)
        ctx.lineTo(centerX + 120, equilibriumY)
        ctx.stroke()
        ctx.fillStyle = "#4ade80"
        ctx.textAlign = "right"
        ctx.fillText("VTCB (O)", centerX - 130, equilibriumY + 4)
        ctx.setLineDash([])

        // Delta L Measurement
        if (deltaL_px > 10) {
          const measureX = centerX + 100
          ctx.strokeStyle = "#94a3b8"
          ctx.beginPath()
          ctx.moveTo(measureX, naturalY)
          ctx.lineTo(measureX + 10, naturalY)
          ctx.moveTo(measureX, equilibriumY)
          ctx.lineTo(measureX + 10, equilibriumY)
          ctx.moveTo(measureX + 5, naturalY)
          ctx.lineTo(measureX + 5, equilibriumY)
          ctx.stroke()

          ctx.fillStyle = "#f8fafc"
          ctx.textAlign = "left"
          ctx.fillText(`Δl₀ = ${(derived.deltaL0 * 100).toFixed(1)} cm`, measureX + 15, naturalY + deltaL_px / 2)
        }

        // Limits
        ctx.strokeStyle = "rgba(250, 204, 21, 0.2)"
        ctx.setLineDash([2, 2])
        ctx.beginPath()
        ctx.moveTo(centerX - 60, equilibriumY - params.amplitude)
        ctx.lineTo(centerX + 60, equilibriumY - params.amplitude)
        ctx.moveTo(centerX - 60, equilibriumY + params.amplitude)
        ctx.lineTo(centerX + 60, equilibriumY + params.amplitude)
        ctx.stroke()
        ctx.setLineDash([])

        // Draw Spring
        ctx.strokeStyle = "#cbd5e1"
        drawSpring(ctx, centerX, ceilingY, centerX, massY - 25, 12, 15)

        // Draw Mass
        ctx.fillStyle = isDragging ? "#60a5fa" : "#3b82f6"
        ctx.beginPath()
        ctx.arc(centerX, massY, 25, 0, Math.PI * 2)
        ctx.fill()
        ctx.strokeStyle = isDragging ? "#93c5fd" : "#60a5fa"
        ctx.lineWidth = isDragging ? 3 : 1
        ctx.stroke()

        // Vectors
        if (!isDragging) {
          drawArrow(ctx, centerX + 30, massY, state.v * 0.5, "#22c55e", "v", "vertical")
          drawArrow(ctx, centerX - 30, massY, state.force * 5, "#ef4444", "F", "vertical")
        }
      }

      // Info Box
      ctx.fillStyle = "rgba(15, 23, 42, 0.7)"
      ctx.fillRect(10, 10, 150, 70)
      ctx.strokeStyle = "rgba(56, 189, 248, 0.3)"
      ctx.strokeRect(10, 10, 150, 70)

      ctx.textAlign = "left"
      ctx.textBaseline = "alphabetic"

      ctx.fillStyle = "#4ade80"
      ctx.font = "13px monospace"
      ctx.fillText(`t = ${t.toFixed(2)} s`, 20, 30)
      ctx.fillStyle = "#38bdf8"
      ctx.fillText(`x = ${state.x.toFixed(1)} cm`, 20, 50)
      ctx.fillStyle = "#facc15"
      ctx.fillText(`v = ${state.v.toFixed(1)} cm/s`, 20, 70)

      if (isDragging) {
        ctx.fillStyle = "#ffffff"
        ctx.font = "12px sans-serif"
        ctx.fillText("✋ Đang kéo...", 20, 95)
      }

      // Signature
      ctx.fillStyle = "rgba(255, 255, 255, 0.1)"
      ctx.font = "12px sans-serif"
      ctx.textAlign = "right"
      ctx.fillText("Hô Hoàng Anh A1K50", currWidth - 15, currHeight - 15)
    },
    [layout, calculateState, params, derived, width, height, isDragging]
  )

  const drawEnergyGraph = useCallback(
    (ctx: CanvasRenderingContext2D) => {
      const w = ctx.canvas.width
      const h = ctx.canvas.height
      const history = historyRef.current

      ctx.fillStyle = "#0f172a"
      ctx.fillRect(0, 0, w, h)

      // Grid
      ctx.strokeStyle = "#1e293b"
      ctx.lineWidth = 0.5
      ctx.beginPath()
      for (let x = 0; x < w; x += 50) {
        ctx.moveTo(x, 0)
        ctx.lineTo(x, h)
      }
      for (let y = 0; y < h; y += 30) {
        ctx.moveTo(0, y)
        ctx.lineTo(w, y)
      }
      ctx.stroke()

      if (history.length < 2) return

      const maxEnergy = 0.5 * params.k * Math.pow(params.amplitude / 100, 2)
      // Safely handle zero maxEnergy
      const yScale = maxEnergy > 0 ? (h * 0.8) / maxEnergy : 1

      const drawLine = (key: keyof typeof history[0], color: string) => {
        ctx.strokeStyle = color
        ctx.lineWidth = 2
        ctx.beginPath()
        const stepX = w / 300
        const dataSlice = history.slice(-300)

        dataSlice.forEach((pt, i) => {
          const val = pt[key] as number
          const x = i * stepX
          const y = h - val * yScale - 20
          if (i === 0) ctx.moveTo(x, y)
          else ctx.lineTo(x, y)
        })
        ctx.stroke()
      }

      drawLine("wd", "#22c55e")
      drawLine("wt", "#3b82f6")
      drawLine("wTotal", "#eab308")
    },
    [params],
  )

  useEffect(() => {
    const render = () => {
      if (isPlaying && !isDragging) {
        step(0.016)
      }

      // Always draw
      if (canvasRef.current && width > 0 && height > 0) {
        const canvas = canvasRef.current

        // Handle HiDPI scaling
        if (canvas.width !== width * pixelRatio || canvas.height !== height * pixelRatio) {
          canvas.width = width * pixelRatio
          canvas.height = height * pixelRatio
          // Don't set style.width here, handled by CSS/container
        }

        const ctx = canvas.getContext("2d")
        if (ctx) {
          ctx.save()
          ctx.scale(pixelRatio, pixelRatio)
          draw(ctx, timeRef.current)
          ctx.restore()
        }
      }

      if (energyCanvasRef.current) {
        const ctx = energyCanvasRef.current.getContext("2d")
        if (ctx) drawEnergyGraph(ctx)
      }

      animationRef.current = requestAnimationFrame(render)
    }
    render()
    return () => {
      if (animationRef.current) cancelAnimationFrame(animationRef.current)
    }
  }, [isPlaying, isDragging, draw, drawEnergyGraph, step, width, height, pixelRatio])

  return (
    <div className="grid grid-cols-1 lg:grid-cols-3 gap-4">
      <div className="lg:col-span-2 space-y-4">
        {/* Main Simulation View - Now Responsive */}
        <div
          ref={containerRef}
          className="bg-[#16213e] rounded-xl overflow-hidden border border-cyan-900/50 shadow-lg relative w-full h-[400px] touch-none"
        >
          <canvas
            ref={canvasRef}
            className="w-full h-full block touch-none cursor-move"
            onPointerDown={handlePointerDown}
            onPointerMove={handlePointerMove}
            onPointerUp={handlePointerUp}
            onPointerLeave={handlePointerUp}
          />
          <div className="absolute top-2 right-2 text-xs text-gray-400 pointer-events-none select-none">
            {layout === "horizontal" ? "Horizontal" : "Vertical"} • Drag mass to set Amplitude
          </div>
        </div>

        {/* CONTROLS */}
        <div className="flex justify-center gap-3 py-2">
          <button
            onClick={reset}
            className="w-12 h-12 rounded-xl bg-slate-700 hover:bg-slate-600 border border-slate-600 text-white flex items-center justify-center transition-all shadow-md active:scale-95"
            title="Reset"
          >
            <RotateCcw size={20} />
          </button>
          <button
            onClick={() => step(-0.05)}
            className="w-12 h-12 rounded-xl bg-slate-700 hover:bg-slate-600 border border-slate-600 text-white flex items-center justify-center transition-all shadow-md active:scale-95"
            title="Lùi"
          >
            <SkipBack size={20} />
          </button>
          <button
            onClick={() => setIsPlaying(!isPlaying)}
            className={`w-14 h-14 rounded-2xl flex items-center justify-center transition-all shadow-lg active:scale-95 border border-transparent ${isPlaying
                ? "bg-amber-600 hover:bg-amber-500 shadow-amber-900/20"
                : "bg-cyan-600 hover:bg-cyan-500 shadow-cyan-500/30"
              } text-white`}
            title={isPlaying ? "Dừng" : "Chạy"}
          >
            {isPlaying ? (
              <Pause size={28} fill="currentColor" />
            ) : (
              <Play size={28} fill="currentColor" className="ml-1" />
            )}
          </button>
          <button
            onClick={() => step(0.05)}
            className="w-12 h-12 rounded-xl bg-slate-700 hover:bg-slate-600 border border-slate-600 text-white flex items-center justify-center transition-all shadow-md active:scale-95"
            title="Tiến"
          >
            <SkipForward size={20} />
          </button>
        </div>

        {/* Energy Graph */}
        <div className="bg-[#16213e] rounded-xl p-4 border border-cyan-900/50 relative">
          <h4 className="text-sm font-bold text-orange-400 mb-2 flex items-center gap-2">
            <TrendingUp className="w-4 h-4" /> Đồ thị Năng lượng
          </h4>
          <canvas
            ref={energyCanvasRef}
            width={800}
            height={200}
            className="w-full h-40 bg-slate-900 rounded border border-slate-700/50"
          />
          <div className="flex justify-center gap-6 mt-2 text-xs font-mono">
            <div className="flex items-center gap-1">
              <div className="w-3 h-1 bg-yellow-500"></div> Cơ năng (W)
            </div>
            <div className="flex items-center gap-1">
              <div className="w-3 h-1 bg-green-500"></div> Động năng (Wđ)
            </div>
            <div className="flex items-center gap-1">
              <div className="w-3 h-1 bg-blue-500"></div> Thế năng (Wt)
            </div>
          </div>
        </div>
      </div>

      <div className="space-y-4">
        <ControlPanel title="Cấu hình hệ thống">
          <div className="flex gap-2 mb-4">
            <button
              onClick={() => setLayout("horizontal")}
              className={`flex-1 py-3 px-2 rounded-lg text-sm font-semibold transition-all border ${layout === "horizontal" ? "bg-cyan-600 border-cyan-500 text-white shadow" : "bg-slate-800 border-slate-700 text-gray-400 hover:bg-slate-700"}`}
            >
              Nằm ngang
            </button>
            <button
              onClick={() => setLayout("vertical")}
              className={`flex-1 py-3 px-2 rounded-lg text-sm font-semibold transition-all border ${layout === "vertical" ? "bg-cyan-600 border-cyan-500 text-white shadow" : "bg-slate-800 border-slate-700 text-gray-400 hover:bg-slate-700"}`}
            >
              Thẳng đứng
            </button>
          </div>

          <div className="space-y-4">
            <div>
              <label className="text-xs text-gray-400 mb-1 block">Khối lượng m</label>
              <div className="flex items-center gap-2">
                <input
                  type="range"
                  min="0.1"
                  max="2"
                  step="0.1"
                  value={params.mass}
                  onChange={(e) => setParams({ ...params, mass: Number(e.target.value) })}
                  className="flex-1 accent-cyan-500 h-2 bg-slate-700 rounded-lg cursor-pointer"
                />
                <span className="w-16 text-right font-mono text-cyan-400 text-sm">{params.mass} kg</span>
              </div>
            </div>
            <div>
              <label className="text-xs text-gray-400 mb-1 block">Độ cứng k</label>
              <div className="flex items-center gap-2">
                <input
                  type="range"
                  min="10"
                  max="200"
                  step="5"
                  value={params.k}
                  onChange={(e) => setParams({ ...params, k: Number(e.target.value) })}
                  className="flex-1 accent-cyan-500 h-2 bg-slate-700 rounded-lg cursor-pointer"
                />
                <span className="w-16 text-right font-mono text-cyan-400 text-sm">{params.k} N/m</span>
              </div>
            </div>
            <div>
              <label className="text-xs text-gray-400 mb-1 block">Biên độ A</label>
              <div className="flex items-center gap-2">
                <input
                  type="range"
                  min="10"
                  max="100"
                  value={params.amplitude}
                  onChange={(e) => {
                    if (!isDragging) setParams({ ...params, amplitude: Number(e.target.value) })
                  }}
                  className="flex-1 accent-cyan-500 h-2 bg-slate-700 rounded-lg cursor-pointer"
                />
                <span className="w-16 text-right font-mono text-cyan-400 text-sm">{Math.round(params.amplitude)} cm</span>
              </div>
            </div>
          </div>
        </ControlPanel>

        <div className="bg-[#1e293b] rounded-xl p-0 border border-cyan-900/50 overflow-hidden">
          <div className="bg-slate-800/50 p-3 border-b border-slate-700 flex items-center gap-2">
            <Activity className="w-4 h-4 text-green-400" />
            <h3 className="text-sm font-bold text-slate-200">Đại lượng dao động</h3>
          </div>
          <div className="p-4 grid grid-cols-2 gap-4">
            <div>
              <div className="text-[10px] text-gray-500 uppercase">Chu kỳ (T)</div>
              <div className="text-lg font-mono font-bold text-white">
                {derived.period.toFixed(2)} <span className="text-xs font-normal text-gray-400">s</span>
              </div>
            </div>
            <div>
              <div className="text-[10px] text-gray-500 uppercase">Tần số (f)</div>
              <div className="text-lg font-mono font-bold text-white">
                {derived.frequency.toFixed(2)} <span className="text-xs font-normal text-gray-400">Hz</span>
              </div>
            </div>
            <div>
              <div className="text-[10px] text-gray-500 uppercase">Vận tốc Max</div>
              <div className="text-lg font-mono font-bold text-green-400">
                {derived.vmax.toFixed(1)} <span className="text-xs font-normal text-gray-400">cm/s</span>
              </div>
            </div>
            <div>
              <div className="text-[10px] text-gray-500 uppercase">Gia tốc Max</div>
              <div className="text-lg font-mono font-bold text-red-400">
                {derived.amax.toFixed(0)} <span className="text-xs font-normal text-gray-400">cm/s²</span>
              </div>
            </div>
            {layout === "vertical" && (
              <div className="col-span-2 bg-slate-900 p-2 rounded border border-slate-700 flex items-center justify-between">
                <span className="text-xs text-yellow-500 flex items-center gap-1">
                  <Ruler size={12} /> Độ giãn lò xo (Δl₀)
                </span>
                <span className="font-mono font-bold text-yellow-400">{(derived.deltaL0 * 100).toFixed(1)} cm</span>
              </div>
            )}
          </div>
        </div>

        <div className="bg-[#16213e] rounded-lg p-4 border border-cyan-900/50">
          <h3 className="text-cyan-400 font-semibold mb-2">Công thức</h3>
          <div className="text-sm text-gray-300 space-y-1 font-mono">
            <p>ω = √(k/m) = {derived.omega.toFixed(2)} rad/s</p>
            <p>T = 2π√(m/k) = {derived.period.toFixed(2)} s</p>
            <p>F = -kx (Định luật Hooke)</p>
          </div>
        </div>
      </div>
    </div>
  )
}