"use client"

import { useRef, useEffect, useState, useCallback } from "react"
import { Zap, RotateCcw, SkipBack, Pause, Play, SkipForward, ChevronLeft, ChevronRight } from "lucide-react"

// Special angles in degrees within one circle (0 to 360)
const SPECIAL_ANGLES_DEG = [0, 30, 45, 60, 90, 120, 135, 150, 180, 210, 225, 240, 270, 300, 315, 330]
const SPECIAL_ANGLES_RAD = SPECIAL_ANGLES_DEG.map((d) => (d * Math.PI) / 180)

export default function HarmonicOscillation() {
  const canvasRef = useRef<HTMLCanvasElement>(null)
  const [isPlaying, setIsPlaying] = useState(false)
  const [time, setTime] = useState(0)

  const [showGraph, setShowGraph] = useState({ x: true, v: true, a: true })

  const [params, setParams] = useState({
    amplitude: 10,
    frequency: 0.5,
    phase: 0,
  })

  const animationRef = useRef<number>()
  const timeRef = useRef(0)

  const omega = 2 * Math.PI * params.frequency
  const T = 1 / params.frequency
  const dt = 0.016

  const calculateState = useCallback(
    (t: number) => {
      const x = params.amplitude * Math.cos(omega * t + params.phase)
      const v = -params.amplitude * omega * Math.sin(omega * t + params.phase)
      const a = -params.amplitude * omega * omega * Math.cos(omega * t + params.phase)
      return { x, v, a }
    },
    [params.amplitude, omega, params.phase],
  )

  const draw = useCallback(
    (ctx: CanvasRenderingContext2D, t: number) => {
      const width = ctx.canvas.width
      const height = ctx.canvas.height
      
      // Layout config
      const circleSectionWidth = 300
      const graphStartX = circleSectionWidth + 40 // More space for labels
      const graphWidth = width - graphStartX - 20
      const centerX = circleSectionWidth / 2
      const centerY = height / 2

      ctx.fillStyle = "#0f172a"
      ctx.fillRect(0, 0, width, height)

      // --- CIRCLE SECTION ---
      
      const state = calculateState(t)
      const angle = omega * t + params.phase
      const radius = 80

      // Circle Grid/Axes
      ctx.strokeStyle = "#1e293b"
      ctx.lineWidth = 1
      ctx.beginPath()
      ctx.moveTo(centerX, centerY - radius - 30)
      ctx.lineTo(centerX, centerY + radius + 30)
      ctx.moveTo(centerX - radius - 30, centerY)
      ctx.lineTo(centerX + radius + 30, centerY)
      ctx.stroke()

      // Main Circle
      ctx.strokeStyle = "#475569"
      ctx.lineWidth = 2
      ctx.beginPath()
      ctx.arc(centerX, centerY, radius, 0, 2 * Math.PI)
      ctx.stroke()

      // Vector
      const vectorX = centerX + radius * Math.cos(angle)
      const vectorY = centerY - radius * Math.sin(angle)

      ctx.strokeStyle = "#f97316"
      ctx.lineWidth = 3
      ctx.beginPath()
      ctx.moveTo(centerX, centerY)
      ctx.lineTo(vectorX, vectorY)
      ctx.stroke()

      ctx.fillStyle = "#f97316"
      ctx.beginPath()
      ctx.arc(vectorX, vectorY, 6, 0, 2 * Math.PI)
      ctx.fill()

      // --- GRAPH SECTION ---

      const activeGraphs = [showGraph.x, showGraph.v, showGraph.a].filter(Boolean).length
      const graphHeight = activeGraphs > 0 ? (height - 40) / activeGraphs : 0
      
      let currentGraphIndex = 0

      // Time Window (History length)
      const timeWindow = 2.5 * T // Show 2.5 periods of history

      const drawGraphLane = (
        type: 'x' | 'v' | 'a', 
        color: string, 
        getValue: (wt: number) => number, 
        maxValue: number,
        label: string,
        unit: string
      ) => {
        if (!showGraph[type]) return

        const laneY = 20 + currentGraphIndex * graphHeight
        const laneCenterY = laneY + graphHeight / 2
        
        // Draw Axis Line
        ctx.strokeStyle = "#334155"
        ctx.lineWidth = 1
        ctx.beginPath()
        ctx.moveTo(graphStartX, laneCenterY)
        ctx.lineTo(graphStartX + graphWidth, laneCenterY)
        ctx.stroke()

        // Y-Axis Labels (Min/Max values)
        ctx.fillStyle = color
        ctx.font = "10px monospace"
        ctx.textAlign = "right"
        // Top (+Max)
        ctx.fillText(`+${maxValue.toFixed(2)}`, graphStartX - 5, laneY + 15)
        // Bottom (-Max)
        ctx.fillText(`-${maxValue.toFixed(2)}`, graphStartX - 5, laneY + graphHeight - 5)
        // Zero
        // ctx.fillStyle = "#64748b"
        // ctx.fillText("0", graphStartX - 5, laneCenterY + 3)

        // Draw T/4 Grid Lines
        // t is at the RIGHT edge (graphStartX + graphWidth)
        // t - timeWindow is at the LEFT edge (graphStartX)
        
        const quarterT = T / 4
        // Calculate the range of k for grid lines: k*T/4
        const tEnd = t 
        const tStart = t - timeWindow
        
        const kStart = Math.ceil(tStart / quarterT)
        const kEnd = Math.floor(tEnd / quarterT)

        ctx.textAlign = "center"
        ctx.font = "10px monospace"

        for (let k = kStart; k <= kEnd; k++) {
            if (k < 0) continue // Don't draw negative time
            const tGrid = k * quarterT
            // Map time to X position
            // x = graphStartX + graphWidth - (tEnd - tGrid) * (graphWidth / timeWindow)
            const xPos = graphStartX + graphWidth - ((tEnd - tGrid) / timeWindow) * graphWidth
            
            // Grid Line
            ctx.strokeStyle = "#334155"
            ctx.lineWidth = 1
            ctx.setLineDash([2, 2])
            ctx.beginPath()
            ctx.moveTo(xPos, laneY)
            ctx.lineTo(xPos, laneY + graphHeight)
            ctx.stroke()
            ctx.setLineDash([])

            // Grid Label (Only draw if it's visible and not too close to edges)
            if (xPos > graphStartX + 10 && xPos < graphStartX + graphWidth + 10) {
                ctx.fillStyle = "#94a3b8"
                let gridLabel = ""
                if (k === 0) gridLabel = "0"
                else if (k % 4 === 0) gridLabel = `${k/4}T`
                else if (k % 2 === 0) gridLabel = `${k/2}T/2`
                else gridLabel = `${k}T/4`
                
                // Draw label at the bottom of the lane
                ctx.fillText(gridLabel, xPos, laneY + graphHeight - 5)
            }
        }

        // Draw Curve
        ctx.strokeStyle = color
        ctx.lineWidth = 2
        ctx.beginPath()
        
        // Draw from Left (Past) to Right (Present)
        const step = 2 
        let started = false
        
        for (let i = 0; i <= graphWidth; i += step) {
            // i=0 corresponds to tStart (Left)
            // i=graphWidth corresponds to tEnd (Right)
            const waveT = tStart + (i / graphWidth) * timeWindow
            
            if (waveT < 0) continue // Don't draw negative time

            const val = getValue(waveT)
            const scale = (graphHeight / 2) * 0.8
            const y = laneCenterY - (val / maxValue) * scale
            
            if (!started) {
                ctx.moveTo(graphStartX + i, y)
                started = true
            } else {
                ctx.lineTo(graphStartX + i, y)
            }
        }
        ctx.stroke()

        // Draw Current Value Dot (at Right Edge)
        const currentVal = getValue(t)
        const currentY = laneCenterY - (currentVal / maxValue) * ((graphHeight / 2) * 0.8)
        
        ctx.fillStyle = color
        ctx.beginPath()
        ctx.arc(graphStartX + graphWidth, currentY, 4, 0, 2 * Math.PI)
        ctx.fill()

        // Draw Label Title
        ctx.font = "bold 12px monospace"
        ctx.fillStyle = color
        ctx.textAlign = "left"
        ctx.fillText(`${label}`, graphStartX + 10, laneY + 20)
        
        // Current Value Text
        ctx.textAlign = "right"
        ctx.fillText(`${getValue(t).toFixed(2)} ${unit}`, graphStartX + graphWidth - 10, laneY + 20)
        
        currentGraphIndex++
      }

      // 1. Draw X (Red)
      drawGraphLane('x', '#ef4444', 
        (wt) => params.amplitude * Math.cos(omega * wt + params.phase), 
        params.amplitude, 
        "Li độ x (cm)", 
        "cm"
      )

      // 2. Draw V (Green)
      drawGraphLane('v', '#22c55e', 
        (wt) => -params.amplitude * omega * Math.sin(omega * wt + params.phase), 
        params.amplitude * omega, 
        "Vận tốc v (cm/s)", 
        "cm/s"
      )

      // 3. Draw A (Blue)
      drawGraphLane('a', '#3b82f6', 
        (wt) => -params.amplitude * omega * omega * Math.cos(omega * wt + params.phase), 
        params.amplitude * omega * omega, 
        "Gia tốc a (cm/s²)", 
        "cm/s²"
      )

      // Labels
      ctx.fillStyle = "#94a3b8"
      ctx.font = "12px monospace"
      ctx.textAlign = "center"
      ctx.fillText("Vòng tròn lượng giác", centerX, height - 10)
      
      // Info overlay on Circle
      ctx.fillStyle = "rgba(15, 23, 42, 0.8)"
      ctx.fillRect(10, 10, 100, 30)
      ctx.strokeStyle = "rgba(56, 189, 248, 0.3)"
      ctx.strokeRect(10, 10, 100, 30)
      ctx.fillStyle = "#e2e8f0"
      ctx.font = "13px monospace"
      ctx.textAlign = "left"
      ctx.fillText(`t = ${t.toFixed(3)}s`, 20, 28)

      // Author
      ctx.fillStyle = "rgba(255, 255, 255, 0.15)"
      ctx.font = "12px monospace"
      ctx.textAlign = "right"
      ctx.fillText("Hô Hoàng Anh A1K50", width - 15, height - 15)
      ctx.textAlign = "left"
    },
    [params.amplitude, params.phase, omega, calculateState, showGraph, T],
  )

  const animate = useCallback(() => {
    const canvas = canvasRef.current
    if (!canvas) return
    const ctx = canvas.getContext("2d")
    if (!ctx) return

    timeRef.current += dt
    // No more MAX_TIME limit
    setTime(timeRef.current)
    draw(ctx, timeRef.current)

    if (isPlaying) {
      animationRef.current = requestAnimationFrame(animate)
    }
  }, [isPlaying, draw])

  useEffect(() => {
    if (isPlaying) {
      animationRef.current = requestAnimationFrame(animate)
    }
    return () => {
      if (animationRef.current) cancelAnimationFrame(animationRef.current)
    }
  }, [isPlaying, animate])

  useEffect(() => {
    const canvas = canvasRef.current
    if (!canvas) return
    const ctx = canvas.getContext("2d")
    if (!ctx) return
    draw(ctx, timeRef.current)
  }, [params, draw, showGraph])

  const handleManualUpdate = (newTime: number) => {
    setIsPlaying(false)
    // Removed MAX_TIME limit
    timeRef.current = Math.max(0, newTime)
    setTime(timeRef.current)
    const canvas = canvasRef.current
    if (!canvas) return
    const ctx = canvas.getContext("2d")
    if (!ctx) return
    draw(ctx, timeRef.current)
  }

  // Handle large jumps
  const handleSkip = (direction: number) => {
    handleManualUpdate(timeRef.current + direction * 0.5) // Jump 0.5s
  }

  const handleReset = () => {
    handleManualUpdate(0)
  }

  // Handle Micro Step (T/80) with snapping to special positions
  const handleMicroStep = (direction: number) => {
    const stepTime = T / 80
    // Small epsilon to prevent floating point issues and getting stuck on a value
    const epsilon = 1e-4 
    
    const currentPhaseTotal = omega * timeRef.current + params.phase
    // Normalize to 0 -> 2PI for comparison
    const currentPhaseNorm = currentPhaseTotal % (2 * Math.PI)
    // Handle negative modulo result
    const currentPhasePos = currentPhaseNorm < 0 ? currentPhaseNorm + 2 * Math.PI : currentPhaseNorm

    let targetTime = timeRef.current + direction * stepTime
    let snapped = false

    // Step Phase in radians
    const stepPhase = (2 * Math.PI) / 80

    if (direction > 0) {
        // Moving forward
        let nextSpecial = SPECIAL_ANGLES_RAD.find(a => a > currentPhasePos + epsilon)
        let distance = 0
        
        if (nextSpecial !== undefined) {
            distance = nextSpecial - currentPhasePos
        } else {
            // Wrap around
            nextSpecial = SPECIAL_ANGLES_RAD[0] + 2 * Math.PI
            distance = nextSpecial - currentPhasePos
        }
        
        // If the distance to the next special angle is within our step size (plus a tiny bit), snap to it
        if (distance <= stepPhase + epsilon) {
            targetTime = timeRef.current + distance / omega
            snapped = true
        }
    } else {
        // Moving backward
        const reversed = [...SPECIAL_ANGLES_RAD].reverse()
        let prevSpecial = reversed.find(a => a < currentPhasePos - epsilon)
        let distance = 0

        if (prevSpecial !== undefined) {
            distance = currentPhasePos - prevSpecial
        } else {
             // Wrap around backward
             prevSpecial = SPECIAL_ANGLES_RAD[SPECIAL_ANGLES_RAD.length - 1] - 2 * Math.PI
             distance = currentPhasePos - prevSpecial
        }

        if (distance <= stepPhase + epsilon) {
             targetTime = timeRef.current - distance / omega
             snapped = true
        }
    }

    handleManualUpdate(targetTime)
  }

  const state = calculateState(time)
  const kineticEnergy = (0.5 * state.v * state.v) / 10000
  const potentialEnergy = (0.5 * omega * omega * state.x * state.x) / 10000

  return (
    <div className="grid grid-cols-1 lg:grid-cols-3 gap-4">
      <div className="lg:col-span-2 space-y-4">
        <div className="bg-[#1e293b] rounded-xl p-4 border border-slate-700/50 relative">
          {/* Toggle buttons */}
          <div className="absolute top-4 right-4 flex gap-2 z-10 bg-[#0f172a]/80 p-1 rounded-lg backdrop-blur-sm border border-slate-700">
            <button
              onClick={() => setShowGraph((p) => ({ ...p, x: !p.x }))}
              className={`px-2 py-1 text-[10px] font-bold rounded transition-all ${showGraph.x ? "bg-red-500 text-white" : "bg-slate-700 text-slate-400"}`}
            >
              x(t)
            </button>
            <button
              onClick={() => setShowGraph((p) => ({ ...p, v: !p.v }))}
              className={`px-2 py-1 text-[10px] font-bold rounded transition-all ${showGraph.v ? "bg-green-500 text-white" : "bg-slate-700 text-slate-400"}`}
            >
              v(t)
            </button>
            <button
              onClick={() => setShowGraph((p) => ({ ...p, a: !p.a }))}
              className={`px-2 py-1 text-[10px] font-bold rounded transition-all ${showGraph.a ? "bg-blue-500 text-white" : "bg-slate-700 text-slate-400"}`}
            >
              a(t)
            </button>
          </div>

          <canvas ref={canvasRef} width={800} height={400} className="w-full rounded-lg bg-[#0f172a]" />
        </div>

        <div className="bg-[#1e293b] rounded-xl p-3 border border-slate-700/50 flex flex-wrap items-center justify-center gap-3">
          <button
            onClick={handleReset}
            className="w-10 h-10 rounded-lg bg-slate-700 hover:bg-slate-600 border border-slate-600 text-white flex items-center justify-center transition-all shadow-md active:scale-95"
            title="Reset về 0"
          >
            <RotateCcw size={16} />
          </button>
          
          <div className="w-[1px] h-8 bg-slate-700 mx-1"></div>

          <button
            onClick={() => handleSkip(-1)}
            className="w-10 h-10 rounded-lg bg-slate-700 hover:bg-slate-600 border border-slate-600 text-white flex items-center justify-center transition-all shadow-md active:scale-95"
            title="Lùi nhanh (0.5s)"
          >
            <SkipBack size={16} />
          </button>

          <button
            onClick={() => handleMicroStep(-1)}
            className="w-12 h-12 rounded-xl bg-slate-800 hover:bg-slate-700 border border-slate-600 text-cyan-400 flex items-center justify-center transition-all shadow-md active:scale-95 ring-1 ring-slate-700/50"
            title="Bước lùi (T/80 + Snap)"
          >
            <ChevronLeft size={24} />
          </button>

          <button
            onClick={() => setIsPlaying(!isPlaying)}
            className={`w-14 h-14 rounded-2xl flex items-center justify-center transition-all shadow-lg active:scale-95 border border-transparent mx-2 ${
              isPlaying
                ? "bg-amber-500 hover:bg-amber-400 shadow-amber-900/20"
                : "bg-cyan-500 hover:bg-cyan-400 shadow-cyan-500/30"
            } text-white`}
            title={isPlaying ? "Dừng" : "Chạy"}
          >
            {isPlaying ? (
              <Pause size={28} fill="currentColor" />
            ) : (
              <Play size={28} fill="currentColor" className="ml-1" />
            )}
          </button>

          <button
            onClick={() => handleMicroStep(1)}
            className="w-12 h-12 rounded-xl bg-slate-800 hover:bg-slate-700 border border-slate-600 text-cyan-400 flex items-center justify-center transition-all shadow-md active:scale-95 ring-1 ring-slate-700/50"
            title="Bước tiến (T/80 + Snap)"
          >
            <ChevronRight size={24} />
          </button>

          <button
            onClick={() => handleSkip(1)}
            className="w-10 h-10 rounded-lg bg-slate-700 hover:bg-slate-600 border border-slate-600 text-white flex items-center justify-center transition-all shadow-md active:scale-95"
            title="Tiến nhanh (0.5s)"
          >
            <SkipForward size={16} />
          </button>
        </div>
      </div>

      {/* Cột phải: Parameters */}
      <div className="space-y-4">
        <div className="bg-[#1e293b] rounded-xl p-4 border border-slate-700/50">
          <h3 className="text-cyan-400 font-semibold mb-4 text-sm">Thông số dao động</h3>
          <div className="space-y-4">
            <div>
              <div className="flex justify-between text-xs mb-1">
                <label className="text-slate-400">Biên độ A</label>
                <span className="text-cyan-400 font-mono">{params.amplitude} cm</span>
              </div>
              <input
                type="range"
                min="20"
                max="150"
                value={params.amplitude}
                onChange={(e) => setParams({ ...params, amplitude: Number(e.target.value) })}
                className="w-full accent-cyan-500 h-2 bg-slate-700 rounded-lg cursor-pointer"
              />
            </div>
            <div>
              <div className="flex justify-between text-xs mb-1">
                <label className="text-slate-400">Tần số f (Chu kỳ T = {(1/params.frequency).toFixed(2)}s)</label>
                <span className="text-cyan-400 font-mono">{params.frequency} Hz</span>
              </div>
              <input
                type="range"
                min="0.1"
                max="2"
                step="0.1"
                value={params.frequency}
                onChange={(e) => setParams({ ...params, frequency: Number(e.target.value) })}
                className="w-full accent-cyan-500 h-2 bg-slate-700 rounded-lg cursor-pointer"
              />
            </div>
            <div>
              <div className="flex justify-between text-xs mb-1">
                <label className="text-slate-400">Pha ban đầu φ</label>
                <span className="text-cyan-400 font-mono">{params.phase.toFixed(2)} rad</span>
              </div>
              <input
                type="range"
                min="0"
                max={Math.PI * 2}
                step="0.1"
                value={params.phase}
                onChange={(e) => setParams({ ...params, phase: Number(e.target.value) })}
                className="w-full accent-cyan-500 h-2 bg-slate-700 rounded-lg cursor-pointer"
              />
            </div>
          </div>
        </div>

        {/* Energy Bars */}
        <div className="bg-[#1e293b] rounded-xl p-4 border border-slate-700/50">
          <h3 className="text-amber-400 font-semibold mb-3 text-sm flex items-center gap-2">
            <Zap size={14} /> Năng lượng
          </h3>
          <div className="space-y-3">
            <div>
              <div className="flex justify-between text-xs mb-1">
                <span className="text-green-400">Động năng Wđ</span>
                <span className="text-green-400 font-mono">{kineticEnergy.toFixed(3)} J</span>
              </div>
              <div className="h-2 bg-slate-700 rounded-full overflow-hidden">
                <div
                  className="h-full bg-green-500 transition-all duration-100"
                  style={{ width: `${(kineticEnergy / (kineticEnergy + potentialEnergy || 1)) * 100}%` }}
                />
              </div>
            </div>
            <div>
              <div className="flex justify-between text-xs mb-1">
                <span className="text-blue-400">Thế năng Wt</span>
                <span className="text-blue-400 font-mono">{potentialEnergy.toFixed(3)} J</span>
              </div>
              <div className="h-2 bg-slate-700 rounded-full overflow-hidden">
                <div
                  className="h-full bg-blue-500 transition-all duration-100"
                  style={{ width: `${(potentialEnergy / (kineticEnergy + potentialEnergy || 1)) * 100}%` }}
                />
              </div>
            </div>
            <div>
              <div className="flex justify-between text-xs mb-1">
                <span className="text-yellow-400">Cơ năng W</span>
                <span className="text-yellow-400 font-mono">{(kineticEnergy + potentialEnergy).toFixed(3)} J</span>
              </div>
              <div className="h-2 bg-yellow-500/30 rounded-full" />
            </div>
          </div>
        </div>

        {/* Formulas */}
        <div className="bg-[#1e293b] rounded-xl p-4 border border-slate-700/50">
          <h3 className="text-cyan-400 font-semibold mb-2 text-sm">Công thức</h3>
          <div className="text-xs text-slate-300 space-y-1 font-mono">
            <p className="flex justify-between">
              <span>x = A·cos(ωt + φ)</span> <span className="text-red-400">Li độ</span>
            </p>
            <p className="flex justify-between">
              <span>v = -Aω·sin(ωt + φ)</span> <span className="text-green-400">Vận tốc</span>
            </p>
            <p className="flex justify-between">
              <span>a = -Aω²·cos(ωt + φ)</span> <span className="text-blue-400">Gia tốc</span>
            </p>
            <div className="w-full h-[1px] bg-slate-700 my-2"></div>
            <p>ω = 2πf = {omega.toFixed(2)} rad/s</p>
            <p>T = 1/f = {(1 / params.frequency).toFixed(2)} s</p>
             <div className="w-full h-[1px] bg-slate-700 my-2"></div>
            <p className="text-slate-400">Vị trí đặc biệt:</p>
            <p>0, ±A/2, ±A/√2, ±A√3/2, ±A</p>
          </div>
        </div>
      </div>
    </div>
  )
}